@import "./reset.css";

:root {
    --background-color-one: rgb(255, 255, 255);
    --background-color-two: rgb(0, 0, 0);
    --dialog-background-color: rgba(0, 0, 0, 0.877);
    --font-color-one: rgb(240, 235, 235);
    --font-color-two: rgb(28, 26, 26);
    --footer-background: rgba(251, 251, 251, 0);
    --background-mask: rgba(0, 0, 0, 0.835);
    --action-button-color: #551985;
    --input-background-color: rgba(26, 26, 26, 0.863);
    --input-border-color: rgba(240, 235, 235, 0.831);
    --shadow: -16px 13px 19px -3px rgba(0, 0, 0, 0.122);
    }

* {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    background-color: #14131b;
    color: #fff;
    position: relative;
}

.flex {
    display: flex;
}

.content-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.rating-star {
    font-size: 2.5em;
}

div {
    h3 {
        text-align: center;

    }
}

.dialog-open-button {
    background-color: var(--action-button-color);
    color: var(--font-color-one);
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    position: absolute;
    bottom: 0%;
    right: 0%;
    transform: translate(-50%, -50%);
}

.dialog-container {
    background-color: rgb(0, 0, 0);
    width: 350px;
    height: 200px;
    justify-content: center;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    h3 {
        padding-top: 15px;
        text-align: center;
    
    }
    p {
        padding: 10px;
        margin-left: 10px;
    }
}

.close-dialog {
    font-size: 24px;
    color: red;
    background-color: #fff;
    border-radius: 2px;
    position: absolute;
    top: 10%;
    right: 0%;
    transform: translate(-50%, -50%);
}